body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html, body{
  color:#343d47;
  padding:0;
  margin:0;
}

.App > .content{
  padding: 20px;
}

.Chart-Wrapper > div{
  display:inline-block;
  margin: 0 auto 0 auto;
}



.LegendDisplay{
    position:relative;
    border: 1px solid #f2f2f2;
    display:inline-block;
    height: 500px;
    min-height: 200px;   
    width: 200px;
    overflow:scroll;
    margin-top:20px;

}

.legendTitle{
    font-weight: 700;
    margin: 0;
    padding:0px;
    text-align: center;
}

hr{
    margin-left: 10%;
    margin-right: 10%;
}
.search{
    width:200px;
}

.search > input{
    width: 90%;
    margin: 0 10px 10px 10px;
}

.dot{
    color: white;
    fill:white;
}
.countryList{
    overflow: scroll; 
}
.countryNameList{
    padding:1.5px;
    cursor: pointer;
    width: 200px;
    margin:2px;
    line-height: 12px;
    text-overflow: ellipsis;
}
.copied{
    display:inline-block;
}

.Footer, .Header{
    max-width: 1200px;
    margin: 0 auto 0 auto;
}

.noCopy{
    position: relative;
    display:none;
    -webkit-animation: slideOut 3s;
            animation: slideOut 3s;
}

@-webkit-keyframes slideout {
    0% {
        top:0px;
    }
    100% {
        top:-20px;
    }
}

@keyframes slideout {
    0% {
        top:0px;
    }
    100% {
        top:-20px;
    }
}
.Chart-Wrapper{
    display:flex;
}

div.tooltip {
    background: white;
    box-shadow: 1px 1px 3px grey;
    height: auto;
    font: 12px sans-serif;
    position: absolute;
    width: 100px;
    padding: 5px;
    pointer-events: none;
  }
  
